.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.homepage {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  max-width: 100%;
  height:auto;
  position: fixed;
}

.homepage-container {
  background-color: #FDF9B1;
  padding: 20px;
  font-size: 2vw;
}

.invisible-btn {
  position: relative;
  margin-left: 65%;
  margin-top: 25%;
  width: 30%;
  height: 10%;
  opacity: 100%;
  cursor: pointer;
  background-color: transparent;
  font-size: 2vw;
  font-family: 'Courier New', Courier, monospace;
  border: none;
  text-decoration: underline;
}

.test-container {
  background-color: #FDF9B1;
  padding: 100px;
  border-radius: 10px;
  max-width: 600px;
  margin: auto;
  text-align: center;
}

.question-section {
  margin-bottom: 20px;
}

.question-text {
  margin-bottom: 20px;
  font-size: 35px;
}

.answer-section button {
  background-color:gainsboro;
  color: black;
  padding: 20px 20px;
  width: 550px;
  margin: 8px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
}

.answer-section button:hover {
  background-color: #45a049;
}

.completion-message {
  font-size: 24px;
}

@media screen and (min-width: 600px) {
  .homepage-container {
    font-size: 3vw;
  }

  .invisible-btn {
    font-size: 3vw;
    margin-top: 30%;
  }
}

@media screen and (max-width: 992px) {
  .homepage-container {
    font-size: 2vw;
  }

  .invisible-btn {
    font-size: 2vw;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
